span{
  display: inline-block;
  font-weight: normal;
  text-overflow:ellipsis;
  // overflow:hidden;
  white-space: nowrap;
}
.small-list-wrap {
  width: 100%;
  height: 100%;
  position: relative;
  .small-list {
    width: 100%;
    height: 100%;
    background-color: #fff;
    user-select:none;
    .smalist-title {
      width: 100%;
      height: 30px;
      line-height: 30px;
      span:last-child {
        border-right: none !important;
      }
    }
    .smalist-content {
      width: 100%;
      height: 100%;
      .smalist-item {
        width: 100%;
        overflow: hidden;
        span {
          overflow: hidden;
          float: left;
          &:last-child {
            // border-right: none !important;
          }
        }
      }
    }
  }
  .table-scroller {
    position: absolute;
    right: 0;
    width: 9px;
    height: 100%;
    top: 0;
    // border-right: 1px solid blue;
    .scroller-top, .scroller-bottom {
      width: 100%;
      height: 9px;
      position: absolute;
      left: 0;
      font-size: 8px;
      // color: #fff;
      text-align: center;
      i {
        position: absolute;
        left: 2px;
        border: 4px solid #fff;
        border-width: 0 3px 5px 3px;
        border-color: transparent transparent #fff transparent;
      }
    }
    .scroller-top {
      top: 0;
      i {
        top: 2px;
      }
    }
    .scroller-bottom {
      bottom: 0;
      i {
        bottom: 2px;
        border-width: 5px 3px 0 3px;
        border-color: #fff transparent transparent transparent;

      }
    }
    .scroller-scrolling {
      width: 7px;
      height: 16px;
      position: absolute;
      left: 1px;
      top: 10px;
      padding: 1px 0;
      border-radius: 7px;
    }
  }
}