span{
  width: 90px;
  display: inline-block;
  font-weight: normal;
  text-overflow:ellipsis;
  // overflow:hidden;
  white-space: nowrap;
  padding: 0 5px;
}
.large-list {
  width: 100%;
  height: 100%;
  position: relative;
  padding-right: 9px;
  user-select:none;
  .table-scroll {
    height: 100%;
    .table-title {
      width: 100%;
      height: 30px;
      border-bottom: 1px solid #aec1da;
      box-sizing: border-box;
      overflow: hidden;
      position: relative;
      div {
        height: 30px;
        line-height: 30px;
        position: absolute;
        top: 0;
        overflow: hidden;
        &:nth-child(1) {
          z-index: 1;
          left: 0;
        }
        &:nth-child(2) {
          z-index: 0;
        }
        span {
          border-right: 1px solid #aec1da;
          position: relative;
          box-sizing: border-box;
          i.width-changer {
            position: absolute;
            right: -4px;
            width: 8px;
            height: 100%;
            cursor: e-resize;
            // background-color: #000;
          }
        }
      }
    }
    .table-content {
      width: 100%;
      // height: calc(100% - 39px);
      height: 100%;
      // overflow: hidden;
      overflow-y: scroll;
      // background-color: #fff;
      .table-item {
        width: 100%;
        height: 30px;
        border-bottom: 1px solid #dfe4ea;
        box-sizing: border-box;
        position: relative;
        overflow: hidden;
        div.item-fixed, div.item-unfixed {
          overflow: hidden;
          position: absolute;
          left: 0;
          top: 0;
          height: 100%;
          z-index: 1;
          span {
            border-right: 1px solid #dfe4ea;
            overflow: hidden;
            height: 100%;
          }
        }
        div.item-unfixed {
          z-index: 0;
        }
        &.stock-normal span{
          color: #9aa4ad;
        }
        &.stock-down span{
          color: #01AA3B;
        }
        &.stock-up span{
          color: #ee2c2c;
        }
      }
    }
  }
  .table-scroller {
    position: absolute;
    right: 0;
    width: 9px;
    // height: calc(100% - 9px);
    height: 100%;
    top: 0;
    // border-right: 1px solid blue;
    .scroller-top, .scroller-bottom {
      width: 100%;
      height: 9px;
      position: absolute;
      left: 0;
      font-size: 8px;
      // color: #fff;
      text-align: center;
      i {
        position: absolute;
        left: 2px;
        border: 4px solid #fff;
        border-width: 0 3px 5px 3px;
        border-color: transparent transparent #fff transparent;
      }
    }
    .scroller-top {
      top: 0;
      i {
        top: 2px;
      }
    }
    .scroller-bottom {
      bottom: 0;
      i {
        bottom: 2px;
        border-width: 5px 3px 0 3px;
        border-color: #fff transparent transparent transparent;

      }
    }
    .scroller-scrolling {
      width: 7px;
      height: 16px;
      position: absolute;
      left: 1px;
      top: 10px;
      padding: 1px 0;
      border-radius: 7px;
    }
  }
  .table-scroller-lr {
    position: absolute;
    // width: calc(100% - 9px);
    width: 100%;
    height: 10px;
    bottom: 0px;
    z-index:2;
    .scroller-left, .scroller-right {
      width: 9px;
      height: 100%;
      position: absolute;
      bottom: 0;
      font-size: 8px;
      // color: #fff;
      text-align: center;
      i {
        position: absolute;
        top: 2px;
        border: 4px solid #fff;
        border-width: 3px 5px 3px 0;
        border-color: transparent#fff transparent transparent;
      }
    }
    .scroller-left {
      left: 0;
      i {
        left: 2px;
      }
    }
    .scroller-right {
      right: 0;
      i {
        right: 2px;
        border-width: 3px 0 3px 5px;
        border-color: transparent transparent transparent #fff;

      }
    }
    .scroller-scrolling {
      width: 16px;
      height: 7px;
      position: absolute;
      top: 1.5px;
      left: 10px;
      padding: 1px 0;
      border-radius: 7px;
    }
  }
}
