@import './font/iconfont.css';
body {margin: 0;font-size: 14px;
  letter-spacing: 1.4px;font-family: "Segoe UI","Segoe UI Web Regular","Segoe UI Symbol","Helvetica Neue","BBAlpha Sans","S60 Sans",Arial,sans-serif;}
body,html{overflow: hidden;-webkit-text-size-adjust: none;width: 100%;height: 100%;-webkit-tap-highlight-color: rgba(0, 0, 0, 0);}

#app {
  height: 100%;
  user-select:none; /* 禁止选中 */
}
*{text-decoration: none;list-style: none;box-sizing: border-box}
img{border: 0px;display: block;}
ul,li,dl,dd,dt,p,ol,h1,h2,h3,h4,h5{padding: 0;margin: 0;}
ul,ol{list-style: none;}
i{font-style: normal;}
a,a:hover{text-decoration: none;color:#333;-webkit-tap-highlight-color: rgba(0, 0, 0, 0);}
.fl{float: left;}
.fr{float: right;}
.clr:after{display:block;clear:both;content:"";visibility:hidden;height:0}
.clr{zoom:1}
.pointer{cursor:pointer;}
a,input,button{ outline:none; padding: 0;margin: 0}
.hide {display: none}
.block {display: block}
.stock-down {color: #01AA3B;}
.stock-up {color: #EF3837;}
input:-webkit-autofill {background-color: #fff;background-image: none;color: #000;}
.notwrap{word-break:keep-all;white-space:nowrap;overflow: hidden;text-overflow: ellipsis;}
[v-cloak]{display: none;}

.clearfix:before,.clearfix:after {
    content: " ";
    display: table;
}
.clearfix:after {clear: both;}
table {
  border-collapse: collapse;
  border-spacing: 0; 
}
td, th {
  padding: 0; 
}
.ellipsis {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.border_bottom_gray {
  border-bottom: 1px solid #E0E0E0;
}
@keyframes twinklingRed {
  0% {
    background-color: #ffd1d1;
  }
}
@keyframes twinklingGreen {
  0% {
    background-color: #b5f7ad;
  }
}
